*{
    margin: 0;
    padding: 0;
    
}
body{
    font-family: 'Poppins', sans-serif;
}

.topbar{
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 48px;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    box-shadow: 0 2px 4px #ccc;
    background-color: #fff;
    justify-content: space-between;
    height: 70px;
}
.topbar-logo{
    display: flex;
    align-items: center;
    margin-right: 36px;
}
.topbar-logo img{
    width: 24px;
    height: 28px;
    margin-right: 8px;
}
.topbar-logo p{
    font-size: 24px;
    letter-spacing: -1px;
    font-weight: 500;
    margin: 0;
    color: rgba(0,0,0,.8);
}
.topbar-left-menu{
    display: flex;
    align-items: center;
}
.topbar-menu-items{
    text-decoration: none;
    color: rgba(0,0,0,.8);
    padding: 24px 0 16px;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: .6px;
    margin: 0 10px;
}
.topbar-menu-items.active{
    color: #20b883;
    padding-bottom: 22px;
    border-bottom: 3px solid #20b883;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#loginform-wrapper{
    margin-top: 150px;
}
.LoginPage{
    width: 100%;
    max-width: 500px;
    border-radius: 6px;
    background-color: #fff;
    margin: 0 auto;
    padding: 32px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    box-shadow: 0 0 6px #ccc;
}
.LoginPage h1{
    letter-spacing: 1px;
    font-weight: 500;
    margin: 0 0 24px;
}
.LoginPage_InputField{
    width: 100%;
    padding-top: 16px;
    font-size: 14px;
    box-sizing: border-box;
    border: 0;
    border-bottom: 2px solid #8c8c8c;
    margin-bottom: 16px;
    outline: none;
}
.LoginPage_Button{
    display: inline-block;
    border-radius: 30px;
    padding: 14px 36px;
    font-size: 12px;
    font-weight: 900;
    letter-spacing: 1px;
    background-color: #20b883;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 24px;
}
.filter-option input{
    border-radius: 24px;
}
.outer-wrapper{
    width: 100%;
    box-sizing: border-box;
    padding: 0 8% 4%;
    margin-top: 100px;
}
.inner-wrapper{
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding: 24px 0;
}
.filter-wrapper{
    width: 180px
}
.filter-option{
    display: flex;
    flex-direction: column;
}
.filter-checkbox{
    margin: 6px 0;
    cursor: pointer;
}
.filter-checkbox input{
    margin-right: 12px;
}
.filter-option p{
    margin:24px 0px ;
}
.order-table{
    width: 100%;
    border-collapse: collapse;
}
.order-table tr{
    vertical-align: middle;
}
.order-table th{
    text-align: left;
    padding: 12px 24px;
    font-size: 14px;
}
.order-table td{
    background-color: #fff;
    border-bottom: 4px solid #f6f7fb;
    text-align: left;
    padding: 12px 24px;
    font-size: 14px;
}
.secondary-text{
    color: #8c8c8c
}
.primary-text{
    color: rgba(0,0,0,.8);
    font-weight: 500;
}









.userlist-filter{
    width: 700px;
    display: flex;
    align-items: center;
    margin: 45px 0px;
}
.userlist-searchbox{
    width: calc(100% - 100px);
    padding: 12px 24px;
    font-size: 15px;
    border: 0;
    border-bottom: 2px solid #8c8c8c;
    margin: 0 36px 0 0;
    outline: none;
}
.userlist-reset-btn{
    display: inline-block;
    border-radius: 30px;
    padding: 14px 36px;
    font-size: 12px;
    font-weight: 900;
    letter-spacing: 1px;
    background-color: #20b883;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer;
}

